import styles from "./X02_GameVideosInfo.module.css";
import { v4 as uuidv4 } from "uuid";

function X02_GameVideosInfo({
  name,
  year,
  platform,
  videoType,
  concerts,
  music,
  icon,
  links,
  linksLength,
  linkshdr,
}) {
  const linksYouTube = links.map((item) => {
    return (
      <div className={styles.linksYouTube__div_box_B} key={uuidv4()}>
        <iframe
          width="100%"
          height="100%"
          src={item}
          title="YouTube video player"
          frameborder="0"
          allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
          referrerpolicy="strict-origin-when-cross-origin"
          allowfullscreen
        ></iframe>
      </div>
    );
  });

  let hdrezka;
  if (linkshdr) {
    if (linkshdr === "") {
      hdrezka = null;
    } else {
      hdrezka = (
        <a
          target="_blank"
          className={styles.game_info__platform}
          href={linkshdr}
        >
          HDrezka
        </a>
      );
    }
  }

  return (
    <div className={styles.game_info__box}>
      <div className={styles.game_info__box_top}>
        <div
          style={{ backgroundImage: `url(${icon})` }}
          className={styles.game_info__icon}
        ></div>

        <div className={styles.game_info__box_text}>
          <h4 className={styles.game_info__title}>{name}</h4>
          <div className={styles.game_info__text}>
            <p className={styles.game_info__year}>
              Year: <span>{year}</span>
            </p>
            <p className={styles.game_info__platform}>
              Platform / Video Type:{" "}
              <span>
                {platform}
                {videoType}
                {concerts}
                {music}
              </span>
            </p>
            <p className={styles.game_info__platform}>
              Links: <span>{linksLength}</span>
            </p>
            {hdrezka}
          </div>
        </div>
      </div>

      <div className={styles.game_info__box_buttom}>{linksYouTube}</div>
    </div>
  );
}

export default X02_GameVideosInfo;
