import { v4 as uuidv4 } from "uuid";
import { useState } from "react";

import styles from "./T02_Filters.module.css";

function T02_Filters({ textIdP, setTextIdP, textIdGNRD, setTextIdGNRD }) {
  const [navPlatformArr, setNavPlatformArr] = useState([
    "Arcade",
    "NES/Fam/Den",
    "GB/GBC",
    "PCE",
    "SMD",
    "SNES",
    "SCD",
    "3DO",
    "S32X",
    "SSat",
    "PS1",
    "N64",
    "SDrem",
    "PS2",
    "GC",
    "GBA",
    "DS",
    "PSP",
    "PS3",
    "Wii",
    "3DS",
    "WiiU",
    "PSPV",
    "Switch",
    "PC+PS4/PS5",
    "All Games",
  ]);

  const [navGameSeriesArr, setNavGameSeriesArr] = useState([
    "TMNT",
    "Sonic",
    "Tekken",
    "Contra",
    "Mega Man",
    "Castlevania",
    "Battletoads",
    "Mortal Kombat",
    "Street Fighter",
  ]);

  const [navVideoTypeArr, setNavVideoTypeArr] = useState([
    "Movie",
    "Cartoon",
    "All Video",
  ]);

  const [navVideoSeriesArr, setNavVideoSeriesArr] = useState([
    "Форсаж",
    "Бэтмен",
    "Вселенная DC",
    "Вселенная Marvel",
    "Гарри Поттер",
    "Обитель зла",
    "Очень страшное кино",
    "Парк Юрского периода",
    "Покемон",
    "Пила",
    "Пираты Карибского моря",
    "Полицейская академия",
    "Средиземье",
    "Черепашки Ниндзя",
    "Земля до начала времен",
    "Терминатор",
    "Рэмбо",
    "Шрек",
    "Рокки",
    "Крепкий орешек",
    "Вселенная «Заклятия»",
    "Наше",
    "Безумный Макс",
    "Скуби-Ду",
    "Человек-паук",
    "Люди Икс",
    "Звёздные войны",
    "Годзилла",
    "Пятница, 13-е",
    "Трансформеры",
    "Чужой",
    "Зубастики",
    "Хищник",
    "Охотники за привидениями",
    "Кошмар на улице Вязов",
  ]);

  const [navConcertsArr, setNavConcertsArr] = useState(["Concerts"]);

  const [navMusicArr, setNavMusicArr] = useState(["Music"]);

  const sortGN = (a, b) => {
    const nameA = a.toUpperCase();
    const nameB = b.toUpperCase();

    if (nameA < nameB) {
      return -1;
    }
    if (nameA > nameB) {
      return 1;
    }
    return 0;
  };
  navGameSeriesArr.sort(sortGN);
  navVideoSeriesArr.sort(sortGN);

  const [navSortingArr, setNavSortingArr] = useState(["Names", "Release Date"]);

  const navPlatform = navPlatformArr.map((item, i) => {
    return (
      <li
        className={styles.navPlatform__li}
        key={uuidv4()}
        onClick={() => {
          setTextIdP((textIdP = navPlatformArr[i]));
        }}
      >
        <a
          className={
            textIdP === navPlatformArr[i] ? styles.navPlatform__li_red : ""
          }
        >
          {item}
        </a>
      </li>
    );
  });

  const navGameSeries = navGameSeriesArr.map((item, i) => {
    return (
      <li
        className={styles.navPlatform__li}
        key={uuidv4()}
        onClick={() => {
          setTextIdP((textIdP = navGameSeriesArr[i]));
        }}
      >
        <a
          className={
            textIdP === navGameSeriesArr[i] ? styles.navPlatform__li_red : ""
          }
        >
          {item}
        </a>
      </li>
    );
  });

  const navVideoType = navVideoTypeArr.map((item, i) => {
    return (
      <li
        className={styles.navPlatform__li}
        key={uuidv4()}
        onClick={() => {
          setTextIdP((textIdP = navVideoTypeArr[i]));
        }}
      >
        <a
          className={
            textIdP === navVideoTypeArr[i] ? styles.navPlatform__li_red : ""
          }
        >
          {item}
        </a>
      </li>
    );
  });

  const navVideoSeries = navVideoSeriesArr.map((item, i) => {
    return (
      <li
        className={styles.navPlatform__li}
        key={uuidv4()}
        onClick={() => {
          setTextIdP((textIdP = navVideoSeriesArr[i]));
        }}
      >
        <a
          className={
            textIdP === navVideoSeriesArr[i] ? styles.navPlatform__li_red : ""
          }
        >
          {item}
        </a>
      </li>
    );
  });

  const navConcerts = navConcertsArr.map((item, i) => {
    return (
      <li
        className={styles.navPlatform__li}
        key={uuidv4()}
        onClick={() => {
          setTextIdP((textIdP = navConcertsArr[i]));
        }}
      >
        <a
          className={
            textIdP === navConcertsArr[i] ? styles.navPlatform__li_red : ""
          }
        >
          {item}
        </a>
      </li>
    );
  });

  const navMusic = navMusicArr.map((item, i) => {
    return (
      <li
        className={styles.navPlatform__li}
        key={uuidv4()}
        onClick={() => {
          setTextIdP((textIdP = navMusicArr[i]));
        }}
      >
        <a
          className={
            textIdP === navMusicArr[i] ? styles.navPlatform__li_red : ""
          }
        >
          {item}
        </a>
      </li>
    );
  });

  const navSorting = navSortingArr.map((item, i) => {
    return (
      <li
        className={styles.navPlatform__li}
        key={uuidv4()}
        onClick={() => {
          setTextIdGNRD((textIdGNRD = navSortingArr[i]));
        }}
      >
        <a
          className={
            textIdGNRD === navSortingArr[i] ? styles.navPlatform__li_red : ""
          }
        >
          {item}
        </a>
      </li>
    );
  });

  return (
    <div className={styles.filters_box}>
      <h3 className={styles.title}>Gaming Platforms</h3>
      <ul className={styles.ul_nav}>{navPlatform}</ul>

      <h3 className={styles.title}>Game Series</h3>
      <ul className={styles.ul_nav}>{navGameSeries}</ul>

      <h3 className={styles.title}>Movie / Cartoone</h3>
      <ul className={styles.ul_nav}>{navVideoType}</ul>

      <h3 className={styles.title}>Movie / Cartoone Series</h3>
      <ul className={styles.ul_nav}>{navVideoSeries}</ul>

      <h3 className={styles.title}>Concerts / Music</h3>
      <ul className={styles.ul_nav}>
        {navConcerts} {navMusic}
      </ul>

      <h3 className={styles.title}>Sortin</h3>
      <ul className={styles.ul_nav}>{navSorting}</ul>
    </div>
  );
}

export default T02_Filters;
