import styles from "./T03_GamesVideos.module.css";
import X01_IconGamesVideos from "../X01_IconGamesVideos/X01_IconGamesVideos";

function T03_GamesVideos({
  textIdP,
  textIdGNRD,
  showHide,
  setShowHide,
  gameInfo,
  setGameInfo,
  setVersionNum,
}) {
  return (
    <div className={styles.games_box}>
      <X01_IconGamesVideos
        textIdP={textIdP}
        textIdGNRD={textIdGNRD}
        showHide={showHide}
        setShowHide={setShowHide}
        gameInfo={gameInfo}
        setGameInfo={setGameInfo}
        setVersionNum={setVersionNum}
      />
    </div>
  );
}

export default T03_GamesVideos;
