import { v4 as uuidv4 } from "uuid";
import { useEffect, useState, useRef } from "react";

import styles from "./X01_IconGamesVideos.module.css";
import X02_GameVideosInfo from "../X02_GameVideosInfo/X02_GameVideosInfo";

import arrAM from "../X03_Arrs_Games/arrAM";
import arrDS from "../X03_Arrs_Games/arrDS";
import arrGBGBC from "../X03_Arrs_Games/arrGBGBC";
import arrNESFD from "../X03_Arrs_Games/arrNESFD";
import arrP3DO from "../X03_Arrs_Games/arrP3DO";
import arrPCE from "../X03_Arrs_Games/arrPCE";
import arrPCPS4PS5 from "../X03_Arrs_Games/arrPCPS4PS5";
import arrPS1 from "../X03_Arrs_Games/arrPS1";
import arrPS2 from "../X03_Arrs_Games/arrPS2";
import arrPS3 from "../X03_Arrs_Games/arrPS3";
import arrPSP from "../X03_Arrs_Games/arrPSP";
import arrSMCD from "../X03_Arrs_Games/arrSMCD";
import arrSMD from "../X03_Arrs_Games/arrSMD";
import arrSNES from "../X03_Arrs_Games/arrSNES";
import arrWii from "../X03_Arrs_Games/arrWii";
import arrWiiU from "../X03_Arrs_Games/arrWiiU";
import arrN64 from "../X03_Arrs_Games/arrN64";
import arrNGC from "../X03_Arrs_Games/arrNGC";
import arrNS from "../X03_Arrs_Games/arrNS";
import arrGBA from "../X03_Arrs_Games/arrGBA";
import arr3DS from "../X03_Arrs_Games/arr3DS";
import arrS32X from "../X03_Arrs_Games/arrS32X";
import arrSS from "../X03_Arrs_Games/arrSS";
import arrSD from "../X03_Arrs_Games/arrSD";
import arrPSPV from "../X03_Arrs_Games/arrPSPV";

import arrCartoons from "../X04_Arrs_Video/arrCartoons";
import arrMovies from "../X04_Arrs_Video/arrMovies";

import arrConcerts from "../X05_Arrs_Concerts/arrConcerts";

import arrMusic from "../X06_Arrs_Music/arrsMusic";

function X01_IconGamesVideos({ textIdP, textIdGNRD, setVersionNum }) {
  const [arrAll, setArrAll] = useState(
    arrNESFD.concat(
      arrSNES,
      arrN64,
      arrNGC,
      arrWii,
      arrWiiU,
      arrNS,
      arrGBGBC,
      arrGBA,
      arrDS,
      arr3DS,
      arrSMD,
      arrS32X,
      arrSMCD,
      arrSS,
      arrSD,
      arrAM,
      arrP3DO,
      arrPCE,
      arrPSP,
      arrPSPV,
      arrPS1,
      arrPS2,
      arrPS3,
      arrPCPS4PS5,

      arrCartoons,
      arrMovies,

      arrConcerts,
      arrMusic
    )
  );

  useEffect(() => {
    setVersionNum(arrAll.length);
  }, [arrAll, setVersionNum]);
  // функция setVersionNum будет вызываться только после первоначальной загрузки данных arrAll и всякий раз, когда arrAll изменяется.
  // Это позволит избежать ошибки "Too many re-renders" (слишком много повторных рендеров)
  // и обеспечит правильное обновление состояния компонента.

  if (textIdGNRD === "Names") {
    const sortGN = (a, b) => {
      const nameA = a.name.toUpperCase();
      const nameB = b.name.toUpperCase();

      if (nameA < nameB) {
        return -1;
      }
      if (nameA > nameB) {
        return 1;
      }
      return 0;
    };

    arrNESFD.sort(sortGN);
    arrSNES.sort(sortGN);
    arrN64.sort(sortGN);
    arrNGC.sort(sortGN);
    arrWii.sort(sortGN);
    arrWiiU.sort(sortGN);
    arrNS.sort(sortGN);
    arrGBGBC.sort(sortGN);
    arrGBA.sort(sortGN);
    arrDS.sort(sortGN);
    arr3DS.sort(sortGN);
    arrSMD.sort(sortGN);
    arrS32X.sort(sortGN);
    arrSMCD.sort(sortGN);
    arrSS.sort(sortGN);
    arrSD.sort(sortGN);
    arrAM.sort(sortGN);
    arrPCE.sort(sortGN);
    arrP3DO.sort(sortGN);
    arrPSP.sort(sortGN);
    arrPSPV.sort(sortGN);
    arrPS1.sort(sortGN);
    arrPS2.sort(sortGN);
    arrPS3.sort(sortGN);
    arrPCPS4PS5.sort(sortGN);
    arrCartoons.sort(sortGN);
    arrMovies.sort(sortGN);
    arrConcerts.sort(sortGN);
    arrMusic.sort(sortGN);
    arrAll.sort(sortGN);
  }

  if (textIdGNRD === "Release Date") {
    const sortRD = (a, b) => {
      return a.year - b.year;
    };

    arrNESFD.sort(sortRD);
    arrSNES.sort(sortRD);
    arrN64.sort(sortRD);
    arrNGC.sort(sortRD);
    arrWii.sort(sortRD);
    arrWiiU.sort(sortRD);
    arrNS.sort(sortRD);
    arrGBGBC.sort(sortRD);
    arrGBA.sort(sortRD);
    arrDS.sort(sortRD);
    arr3DS.sort(sortRD);
    arrSMD.sort(sortRD);
    arrS32X.sort(sortRD);
    arrSMCD.sort(sortRD);
    arrSS.sort(sortRD);
    arrSD.sort(sortRD);
    arrAM.sort(sortRD);
    arrPCE.sort(sortRD);
    arrP3DO.sort(sortRD);
    arrPSP.sort(sortRD);
    arrPSPV.sort(sortRD);
    arrPS1.sort(sortRD);
    arrPS2.sort(sortRD);
    arrPS3.sort(sortRD);
    arrPCPS4PS5.sort(sortRD);
    arrCartoons.sort(sortRD);
    arrMovies.sort(sortRD);
    arrConcerts.sort(sortRD);
    arrMusic.sort(sortRD);
    arrAll.sort(sortRD);
  }

  // /////////////////////////////////////////////////////////////////////////////////////////////

  const [idItem, setIdItem] = useState("");
  const [trueFalse, setTrueFalse] = useState(false);

  const iconsAll = arrAll.map((item, i) => {
    const icon = (
      <div
        className={
          trueFalse && idItem === i ? styles.icon_item_open : styles.icon_item
        }
        style={{ backgroundImage: `url(${item.icon})` }}
        key={uuidv4()}
        onClick={() => {
          setTrueFalse(true);
          setIdItem(i);
        }}
      >
        {trueFalse && idItem === i ? (
          <>
            <X02_GameVideosInfo
              name={item.name}
              year={item.year}
              platform={item.platform}
              videoType={item.videoType}
              concerts={item.concerts}
              music={item.music}
              icon={item.icon}
              links={item.links}
              linksLength={item.links.length}
              linkshdr={item.linkshdr}
            />
          </>
        ) : (
          <span className={styles.icon_text}>
            {item.platform}
            {item.videoType}
            {item.concerts}
            {item.music}
          </span>
        )}
      </div>
    );

    if (textIdP === item.platform) {
      return <>{icon}</>;
    } else if (textIdP === item.series) {
      return <>{icon}</>;
    } else if (textIdP === item.series2) {
      return <>{icon}</>;
    } else if (textIdP === item.videoType) {
      return <>{icon}</>;
    } else if (textIdP === item.concerts) {
      return <>{icon}</>;
    } else if (textIdP === item.music) {
      return <>{icon}</>;
    } else if (textIdP === "All Games" && item.platform) {
      return <>{icon}</>;
    } else if (textIdP === "All Video" && item.videoType) {
      return <>{icon}</>;
    }
  });

  return (
    <>
      {iconsAll}
      <div
        className={styles.game_info__btm_close}
        onClick={() => {
          setTrueFalse(false);
        }}
      ></div>
    </>
  );
}

export default X01_IconGamesVideos;
